.modal .form-control {
    font-size: 13px;
}

.upload-icon-wrap {
    color: #ccc;
    text-align: center;
    margin: 20px 20px 15px!important;
    padding: 5px!important;
    font-size: 65px;
    cursor: pointer;
    border: 2px dotted #f1f1f1;
    transition: .25s;
    &:hover, &:active, &:focus {
        color: #666;
        border-color: #ccc;
    }
    .upload-icon {
        cursor: pointer;
    }
}


